.contentContainer{
    display: flex;
    flex-direction: row;
    position: relative;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 90%; 
    height:auto;
    background-color: #fff;

    padding-left: 1rem;
    margin: auto;
    margin-top: 1rem;
    border-radius: 25px;
    
    box-shadow: 0px 4px 6px 4px rgba(91,164,164,0.2);
    &:hover{
        box-shadow: 0px 4px 6px 4px rgba(50, 90, 90, 0.2);
    }
    .post{
        display: flex;
        flex-direction: column;
        position: relative;
        width: 40%;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 4px 6px 4px rgba(50, 90, 90, 0.2);
        border-radius: 25px;
        margin: 1rem;
        padding: 1rem;
        @media only screen and (max-width:700px){
            width: 90%;

        }
        .postImage{
            display: flex;
            align-items: center;
            justify-content: center;
            
            img{
                width: 100%;
                justify-content: baseline;
                border-radius: 25px;
                
            }
        }
        .postTitle{
            display: block;
            color: #000000;
            font-weight:bold;
            font-size: 150%;
            padding:1rem;
            
        }
        .postInfo{
            flex-direction: row;
            align-items: center;
            display: flex;
            justify-content: center;
            padding: 1rem;
            .postCreator{
                color: #3b3a3a;
                font-size: 1rem;
                font-weight: bold;
                padding:1rem;
                text-decoration: none;
            }
            .postCreatedAt{
                color: #3b3a3a;
                padding: 1rem;
                font-size: 1rem;
                word-wrap: unset;
            }
        }
        .postDescription{
            display: flex;
            flex-direction: row;
            .postContent{
                flex:2;
                padding: 1rem;
                color: #3b3a3a;
                font-size: 1rem;
                text-decoration: none;
            }
            .postShowMore{
                flex:1;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: auto;
                color: #3b3a3a;
                font-size: 1rem;
                padding: 1rem;
                margin:1rem;
                text-decoration: none;
                border-radius: 25px;
                height: 3rem;
                box-shadow: 0px 4px 6px 4px rgba(91,164,164,0.2);
                &:hover{
                    box-shadow: 0px 4px 6px 4px rgba(50, 90, 90, 0.2);
                }
            }
        }
        
    }
}