@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
:root{--primary-color:hsl(157, 65%, 92%);--background-color:hsl(0, 1%, 17%);--filter-table:hsl(180, 31%, 95%);--dark-greyish:hsl(180, 8%, 52%);--very-dark-greyish:hsl(180, 14%, 20%);--dark-green:rgb(10, 131, 78);--light-green:rgb(75, 167, 127)}*{margin:0;padding:0;box-sizing:inherit}html{font-size:15px;font-weight:500;box-sizing:border-box}body{font-family:"Spartan",sans-serif;background-color:hsl(0, 1%, 17%);background-color:var(--background-color)}@media only screen and (max-width: 375px){html{font-size:.75rem}}.cardList{display:flex;flex-wrap:wrap}.loading{margin-top:20vw;text-align:center;font-weight:700;font-size:40px}.headerContainer{display:flex;flex-direction:row;position:relative;width:90%;background-color:#fff;padding:0rem 1rem 1rem;margin:auto;margin-top:2rem;border-radius:25px;box-shadow:0px 4px 6px 4px rgba(91,164,164,.2)}.headerContainer:hover{box-shadow:0px 4px 6px 4px rgba(50,90,90,.2)}.headerContainer .navBar{display:flex;flex-direction:row;list-style:none;margin-top:1rem;float:left;padding:0}.headerContainer .navBar li{display:block;color:#000;font-weight:bold;padding:8px 16px;text-decoration:none}.headerContainer .searchBar{flex-grow:1}.headerContainer .searchBar input{width:20%;float:right;padding:6px;border:none;margin-top:1rem;margin-right:1rem;font-size:17px}.contentContainer{display:flex;flex-direction:row;position:relative;flex-wrap:wrap;justify-content:space-evenly;align-items:center;width:90%;height:auto;background-color:#fff;padding-left:1rem;margin:auto;margin-top:1rem;border-radius:25px;box-shadow:0px 4px 6px 4px rgba(91,164,164,.2)}.contentContainer:hover{box-shadow:0px 4px 6px 4px rgba(50,90,90,.2)}.contentContainer .post{display:flex;flex-direction:column;position:relative;width:40%;align-items:center;justify-content:center;box-shadow:0px 4px 6px 4px rgba(50,90,90,.2);border-radius:25px;margin:1rem;padding:1rem}@media only screen and (max-width: 700px){.contentContainer .post{width:90%}}.contentContainer .post .postImage{display:flex;align-items:center;justify-content:center}.contentContainer .post .postImage img{width:100%;justify-content:baseline;border-radius:25px}.contentContainer .post .postTitle{display:block;color:#000;font-weight:bold;font-size:150%;padding:1rem}.contentContainer .post .postInfo{flex-direction:row;align-items:center;display:flex;justify-content:center;padding:1rem}.contentContainer .post .postInfo .postCreator{color:#3b3a3a;font-size:1rem;font-weight:bold;padding:1rem;text-decoration:none}.contentContainer .post .postInfo .postCreatedAt{color:#3b3a3a;padding:1rem;font-size:1rem;word-wrap:unset}.contentContainer .post .postDescription{display:flex;flex-direction:row}.contentContainer .post .postDescription .postContent{flex:2 1;padding:1rem;color:#3b3a3a;font-size:1rem;text-decoration:none}.contentContainer .post .postDescription .postShowMore{flex:1 1;display:flex;justify-content:center;align-items:center;margin:auto;color:#3b3a3a;font-size:1rem;padding:1rem;margin:1rem;text-decoration:none;border-radius:25px;height:3rem;box-shadow:0px 4px 6px 4px rgba(91,164,164,.2)}.contentContainer .post .postDescription .postShowMore:hover{box-shadow:0px 4px 6px 4px rgba(50,90,90,.2)}.back img{margin:1rem;margin-bottom:0;width:5rem;border-radius:50px;box-shadow:0px 4px 6px 4px rgba(128,128,128,.2)}.back img:hover{box-shadow:0px 16px 24px 16px rgba(0,0,0,.2)}.detail{display:flex;flex-direction:column;position:relative;width:90%;align-items:center;justify-content:center;box-shadow:0px 4px 6px 4px rgba(50,90,90,.2);border-radius:25px;margin:1rem;padding:1rem}@media only screen and (max-width: 700px){.detail{width:90%}}.detail .detailImage{display:flex;align-items:center;justify-content:center}.detail .detailImage img{width:100%;justify-content:baseline;border-radius:25px}.detail .detailTitle{display:block;color:#000;font-weight:bold;font-size:150%;padding:1rem}.detail .detailInfo{flex-direction:row;align-items:center;display:flex;justify-content:center;padding:1rem}.detail .detailInfo .detailCreator{color:#3b3a3a;font-size:1rem;font-weight:bold;padding:1rem;text-decoration:none}.detail .detailInfo .detailCreatedAt{color:#3b3a3a;padding:1rem;font-size:1rem;word-wrap:unset}.detail .detailDescription{display:flex;flex-direction:row}.detail .detailDescription .detailContent{flex:2 1;padding:1rem;color:#3b3a3a;font-size:1rem;text-decoration:none}
